import React from 'react'
import '../styles/typeCard.css'
import { Link } from 'gatsby'

export default function TypeCard(props) {
    return (
        <Link to='/'>
            <div className={props.cardMargin === 'true' ? 'type-card margin-true' : props.cardMargin === 'true1' ? 'type-card margin-true1' : 'type-card'}>
                <img src={props.imgSource} alt={props.imgAlt} />
                <div>
                    <p className={props.titleColor}>{props.cardTitle}</p>
                    <p>{props.cardText}</p>
                </div>
            </div>
        </Link>
    )
}